body {
  font-family: 'Inter', 'Segoe UI', 'Arial', sans-serif;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  margin: 0;
}

.flashcard-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #e0e7ff 0%, #f0abfc 100%);
  padding: 2rem;
}

.flashcard-box {
  background: rgba(255,255,255,0.95);
  border-radius: 1.5rem;
  box-shadow: 0 8px 32px 0 rgba(26, 26, 46, 0.25);
  padding: 2.5rem 2rem;
  max-width: 650px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1.5px solid #e0e7ef;
}

.flashcard-title {
  font-size: 2.7rem;
  font-weight: 900;
  color: #0f3460;
  margin-bottom: 1.5rem;
  text-align: center;
  letter-spacing: 1px;
  text-shadow: 0 2px 8px #e0e7ef;
}

.flashcard-chapter {
  color: #5a189a;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  text-align: center;
  font-weight: 600;
}

.flashcard-count {
  color: #22223b;
  font-size: 1.05rem;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 500;
}

.flashcard-card {
  background: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
  border-radius: 1rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  width: 100%;
  min-height: 180px;
  box-shadow: 0 2px 8px 0 rgba(59, 7, 100, 0.07);
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}
.flashcard-card:hover {
  transform: scale(1.03);
  box-shadow: 0 4px 16px 0 rgba(26, 26, 46, 0.13);
}
.flashcard-question {
  font-size: 1.3rem;
  font-weight: 700;
  color: #22223b;
  margin-bottom: 1rem;
  text-align: center;
}
.flashcard-options {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
.flashcard-option {
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
  background: #fff;
  color: #22223b;
  font-size: 1.05rem;
  font-weight: 500;
  transition: background 0.2s, color 0.2s;
  border: 1px solid #e0e7ef;
}
.flashcard-option.correct {
  background: #b7e4c7;
  color: #186049;
  font-weight: 700;
  box-shadow: 0 0 0 2px #38bdf8;
}
.flashcard-answer {
  margin-top: 1rem;
  font-size: 1.1rem;
  font-weight: 700;
  color: #186049;
  letter-spacing: 0.5px;
}
.flashcard-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}
.flashcard-btn {
  padding: 0.75rem 2rem;
  background: linear-gradient(90deg, #5a189a 0%, #38bdf8 100%);
  color: #fff;
  font-weight: 600;
  border: none;
  border-radius: 999px;
  box-shadow: 0 2px 8px 0 rgba(26, 26, 46, 0.10);
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
}
.flashcard-btn:hover {
  background: linear-gradient(90deg, #38bdf8 0%, #5a189a 100%);
  transform: scale(1.05);
}
